<template lang="pug">
  div
    textarea(v-model="map")
    textarea(v-model="time")
    textarea(v-model="desc")

    input.input(v-model="link" placeholder="link")
    input.input(v-model="cargo" placeholder="cargo")
    input.input(v-model="discord" placeholder="discord")

    div.radio
      label.radio
        input(type="radio" v-model="route" value="Оптимальный")
        | Оптимальный
      label.radio
        input(type="radio" v-model="route" value="Кратчайший")
        | Кратчайший
      label.radio
        input(type="radio" v-model="route" value="По региональным дорогам")
        | По региональным дорогам

    div.checkbox
      label.checkbox
        input(type="checkbox" v-model="dlc" value="Beyond the Baltic Sea")
        | Beyond the Baltic Sea
      label.checkbox
        input(type="checkbox" v-model="dlc" value="Going East!")
        | Going East!
      label.checkbox
        input(type="checkbox" v-model="dlc" value="Italia")
        | Italia
      label.checkbox
        input(type="checkbox" v-model="dlc" value="Scandinavia")
        | Scandinavia
      label.checkbox
        input(type="checkbox" v-model="dlc" value="Vive la France !")
        | Vive la France !
 

    button(@click="isAnnounce(array)") Click
    div.vk
      pre(v-model="info") {{ info.server }} 
        br
        | {{ info.contact }}
        br
        | {{ info.route }}
        br
        | {{ info.other }}
        br
        br
      pre(v-model="info" v-for="item in info.dlc")
        | {{ item }}
      pre(v-model="info")
        | Старт: {{ info.startCity }} ( {{ info.startCompany }} )
        br
        | Финиш: {{ info.finishCity }} ( {{ info.finishCompany }} )
        br
        | {{ info.meet }}
        br
        | {{ info.exit }}
        br
        | {{ info.cargo }}
    
    hr
    hr
      
    div.discord
      pre(v-model="info") @everyone 
        br
        | Старт: {{ info.startCity }}
        br
        | {{ info.meet }}
        br
        | Подробнее:
        br
        | https://vk.com/vtcda
        br
        | {{ info.vtcpanel }}

    textarea.textarea__bottom(placeholder="vk")
    textarea.textarea__bottom(placeholder="discord")
    
</template>


<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      map: "",
      time: "",
      desc: "",
      link: "",
      cargo: "Груз: ",
      route: "Оптимальный",
      discord: "",
      dlc: [],
      array: [],
      info: {
        startCity: "",
        startCompany: "",
        finishCity: "",
        finishCompany: "",
        server: "",
        meet: "",
        exit: "",
        contact: "",
        other: "",
        vtcpanel: "",
        cargo: "",
        route: "",
        dlc: []
      }
    };
  },
  methods: {
    isAnnounce(arr) {
      if (this.link !== "") {
        arr = this.map.split("\n");
        arr.splice(0, 1);
        arr.splice(2, 4);

        let arr2 = this.time.split("\n");
        arr2.splice(3);
        arr2.forEach(el => {
          arr.push(el);
        });
        let n = 0;
        for (let item in this.info) {
          this.info[item] = arr[n];
          n++;
        }
        this.info.other = this.desc;
        this.info.cargo = this.cargo;
        this.info.vtcpanel = this.link;
        this.info.route = `Предпочитаемый маршрут: ${this.route}`;
        if (this.discord === "") {
          this.info.contact = "Связь: https://discord.gg/JSGZVph";
        } else {
          this.info.contact = this.discord;
        }
      }
      if (this.dlc.length !== 0) {
        this.info.dlc = [];
        this.dlc.forEach(el => {
          this.info.dlc.push(el);
        });
      }
    }
  }
};
</script>

